.noproject {
  width: 100%;
  color: #fff;
  display: grid;
  grid-column: 1/3;
  text-align: center;
  align-content: center;
  height: 30vh;

  &__title {
    font-size: 45px;
    margin-bottom: 18px;
    font-weight: bold;
  }
  &__text {
    margin-bottom: 16px;
    font-size: 18px;
  }
  &__icon {
    color: #fff;
    font-size: 45px;

    &:hover {
      transition: 0.2s;
      color: red;
    }
  }
}